@import url('https://fonts.googleapis.com/css?family=Lato:400,700&display=swap');

.App {
  text-align: center;
  padding: 2% 15%;
  font-family: 'Lato', sans-serif;
}

.App * {
  box-sizing: border-box;
}

ul {
  padding: 0;
  list-style: none;
}

p {
  margin: 5px 0;
}

input[type="text"],
input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: 4px;
  outline: none;
  border: 1px solid #ccc;
  font-size: 14px;
  line-height: 20px;
  align-self: center;
  height: 38px;
}

.city {
  width: 250px;
}

.city-search,
.number-of-events {
  display: flex;
  flex-direction: column;
}

.suggestions {
  width: 250px;
  align-self: center;
  margin: 0;
  font-size: 14px;
  position: absolute;
  top: 105px;
  z-index: 1;
}

.suggestions>li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: #eee;
  cursor: default;
}

.suggestions>li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}


.suggestions>li:hover {
  background-color: #BCDCDB;
}

.event {
  padding: 10px 10px 20px;
  margin-bottom: 10px;
  text-align: left;
  border: 1px solid #ccc;
  border-radius: 4px;
  position: relative;
}

.event-name,
.label {
  font-weight: 700;
}

.event-name {
  font-size: 24px;
}

.event-count {
  font-style: italic;
}

.event-link:link,
.event-link:visited {
  color: blue;
}

.details-btn {
  position: absolute;
  right: 5px;
  bottom: 5px;
  padding: 5px 8px;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
}

.name {
  font-size: 18px;
  font-weight: 600;
}

.group-name {
  text-transform: uppercase;
  font-size: 14px;
}

.going,
.visibility {
  font-style: italic;
}

.event-description {
  overflow-x: hidden;
}

.number-of-events {
  margin: 20px 0 50px;
}

.number{
  text-align: right;
  width: 100px;
}

@media only screen and (max-width: 700px) {
  .App {
    padding: 2% 5%;
  }

  .event-name{
    font-size: 18px;
  }
}